import { Pie, PieChart } from 'recharts';
import { ChartConfig, ChartContainer, ChartTooltip, ChartTooltipContent } from '@/components/ui/chart';

interface Props {
    chartData?: any[];
    chartConfig: ChartConfig;
}

export function PieChartComponent({ chartData, chartConfig }: Props) {
    return (
        <div className="flex flex-col min-w-[250px]">
            <ChartContainer config={chartConfig} className="mx-auto aspect-square min-h-[250px]">
                <PieChart
                    margin={{
                        top: 0,
                        bottom: 0,
                        right: 0,
                        left: 0,
                    }}
                >
                    <ChartTooltip cursor={false} content={<ChartTooltipContent hideLabel />} />
                    <Pie data={chartData} dataKey="visitors" nameKey="days" innerRadius={60} strokeWidth={5}></Pie>
                </PieChart>
            </ChartContainer>
        </div>
    );
}
